@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-family: "Open Sans", "Arial", sans-serif;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.btn {
  display: block;
  padding: 15px 20px;
  font-size: 18px;
  line-height: 18px;
  font-weight: 700;
  text-decoration: none;
  text-align: center;
  border: 3px solid;
  border-radius: 500px;
  cursor: pointer;
}
.slider {
  display: flex;
  justify-content: space-between;
  width: 63px;
}
.slider__btn {
  width: 12px;
  height: 12px;
  padding: 0;
  cursor: pointer;
  background-color: #bec2c7;
  border-radius: 50%;
  border: 3px solid;
  border-color: inherit;
}
.slider__btn--current {
  width: 12px;
  height: 12px;
  background-color: #ffffff;
  border: 3px solid #283645;
}
@media (min-width: 660px) {
}
@media (min-width: 960px) {
}
.page-header {
  position: relative;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 10;
}
@media (min-width: 960px) {
  .page-header {
    min-height: 109px;
  }
  .page-header__container {
    margin: 0 auto;
    width: 960px;
  }
}
@media (max-width: 959px) {
  .menu {
    position: relative;
    color: #ffffff;
  }
  .menu--colored {
    background-color: #283645;
  }
  .menu--transparent {
    background-color: transparent;
  }
  .menu--closed .menu__list--site {
    display: none;
  }
  .menu__item {
    box-shadow: inset 0 -1px 0 1px #000000, inset 0 1px 0 1px rgba(255, 255, 255, 0.15);
  }
  .menu--closed .menu__item {
    box-shadow: none;
  }
  .menu__btn {
    position: absolute;
    right: 0;
    top: 0;
    display: block;
    height: 67px;
    padding: 0 20px;
    background-color: #283645;
    cursor: pointer;
    border: none;
    outline: none;
  }
  .menu__btn:hover path,
  .menu__btn:focus path {
    fill: #d22856;
  }
  .menu__btn:active path {
    fill: rgba(210, 40, 86, 0.3);
  }
  .menu__icon--burger,
  .menu__icon--cross {
    fill: #ffffff;
  }
  .menu__icon--burger:hover,
  .menu__icon--cross:hover {
    fill: #d22856;
  }
  .menu--nojs .menu__btn {
    display: none;
  }
  .menu--closed .menu__btn {
    background-color: transparent;
  }
  .menu--opened .menu__icon--burger {
    display: none;
  }
  .menu--closed .menu__icon--cross {
    display: none;
  }
  .menu__list {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  .menu__link {
    display: inline-block;
    box-sizing: border-box;
    width: 100%;
    padding: 17px 15px;
    color: #ffffff;
    text-transform: uppercase;
    font-size: 18px;
    line-height: 32px;
    font-weight: 700;
    text-decoration: none;
    text-align: center;
    outline: none;
  }
  .menu__link[href]:hover,
  .menu__link[href]:focus {
    color: #d22856;
  }
  .menu__link[href]:active {
    color: rgba(210, 40, 86, 0.3);
  }
  .menu__link--logo {
    padding-top: 22px;
    padding-bottom: 25px;
    width: 110px;
    text-align: left;
    line-height: 0;
  }
  .menu__link--logo[href]:hover path,
  .menu__link--logo[href]:focus path {
    fill: #d22856;
  }
  .menu__link--logo[href]:active path {
    fill: rgba(210, 40, 86, 0.3);
  }
  .menu__link--logo svg {
    width: 75px;
    height: 22px;
  }
}
@media (max-width: 660px) {
  .icon-logo__small {
    display: inline-block;
    width: 75px;
    height: 22px;
  }
  .icon-logo__middle {
    display: none;
  }
  .icon-logo__large {
    display: none;
  }
}
@media (min-width: 660px) and (max-width: 959px) {
  .menu__btn {
    height: 107px;
  }
  .menu__link--logo {
    padding-top: 27px;
    padding-bottom: 42px;
    width: 190px;
  }
  .menu__link--logo svg {
    width: 147px;
    height: 40px;
  }
  .icon-logo__small {
    display: none;
  }
  .icon-logo__middle {
    display: inline-block;
    width: 147px;
    height: 40px;
  }
  .icon-logo__large {
    display: none;
  }
}
@media (min-width: 960px) {
  .menu {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0 auto;
    padding: 0;
    max-width: 940px;
  }
  .menu--colored {
    background-color: transparent;
  }
  .menu__list {
    display: flex;
    flex-direction: row;
    margin: 0;
    padding: 0;
    list-style: none;
  }
  .menu__list--logo {
    flex-grow: 1;
    margin-right: auto;
  }
  .menu__list--site {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    flex-grow: 2;
    margin-left: auto;
    padding-left: 50px;
  }
  .menu__list--site .menu__item {
    margin-left: 50px;
    box-shadow: none;
  }
  .menu__item--active {
    position: relative;
  }
  .menu__item--active::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -10px;
    height: 2px;
    width: 100%;
    background-color: #ffffff;
  }
  .menu__link {
    display: inline-block;
    width: auto;
    text-transform: none;
    font-size: 18px;
    line-height: 18px;
    font-weight: 400;
    color: #ffffff;
    text-decoration: none;
  }
  .menu__link[href]:hover,
  .menu__link[href]:focus {
    color: #d22856;
  }
  .menu__link[href]:active {
    color: rgba(255, 255, 255, 0.3);
  }
  .menu__link--logo {
    padding-top: 27px;
    padding-bottom: 42px;
    text-align: left;
    line-height: 0;
  }
  .menu__link--logo[href]:hover path,
  .menu__link--logo[href]:focus path {
    fill: #d22856;
  }
  .menu__link--logo[href]:active path {
    fill: rgba(255, 255, 255, 0.3);
  }
  .menu__link--logo svg {
    width: 147px;
    height: 40px;
  }
  .menu__btn {
    display: none;
  }
  .icon-logo__small {
    display: none;
  }
  .icon-logo__middle {
    display: none;
  }
  .icon-logo__large {
    display: inline-block;
    width: 147px;
    height: 40px;
  }
}
