.price {
  padding-top: 133px;
  padding-bottom: 47px;
  background-image: linear-gradient(175deg, white 14%, #f2f2f2 14%);
}
.price__container {
  margin: 0 auto;
  overflow: hidden;
  padding: 0 20px;
}
.price__table {
  display: flex;
  flex-direction: row;
  position: relative;
  left: 0;
  transform: translateX(-200%);
}
.price__column {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  width: 100%;
  background-color: #ffffff;
}
.price__data {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  box-sizing: border-box;
  min-width: 280px;
  min-height: 60px;
  padding-top: 18px;
  padding-left: 20px;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  color: #283645;
  text-align: left;
  text-transform: uppercase;
  border: 1px solid #e5e5e5;
}
.price__data--header {
  position: relative;
  box-sizing: border-box;
  min-height: 100px;
  padding: 22px;
  padding-bottom: 12px;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  background-color: #283645;
  border: 1px solid #e5e5e5;
}
.price__data--empty {
  background-color: #f2f2f2;
  border: none;
  border-bottom: 1px solid #e5e5e5;
}
.price__data--yes,
.price__data--no {
  position: relative;
}
.price__data--yes::after,
.price__data--no::after {
  content: "";
  position: absolute;
  top: 15px;
  right: 15px;
  width: 31px;
  height: 27px;
  background-repeat: no-repeat;
  background-position: center;
}
.price__data--yes::after {
  background-image: url("../img/icon-yes.svg");
  background-size: 31px;
}
.price__data--no::after {
  background-image: url("../img/icon-no.svg");
  background-size: 27px;
}
.price__rate-name {
  margin-top: 0;
  margin-bottom: 12px;
  font-weight: 700;
  font-size: 18px;
}
.price__rate-name--hit::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 74px;
  height: 73px;
  background-image: url("../img/bg-hit.svg");
}
.price__rate-name--hit::after {
  content: "хит";
  position: absolute;
  top: 8px;
  left: 8px;
  width: 74px;
  height: 73px;
  font-weight: 400;
  font-size: 14px;
  transform: rotate(-45deg);
}
.price__rate-price {
  margin: 0;
  font-weight: 300;
  font-size: 24px;
}
.price__slider {
  margin: 0 auto;
  margin-top: 42px;
  border-color: #f2f2f2;
}
